<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}預約`" :routeTarget="{name: 'ScheduleBreak'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <p class="my-2 text-center">新增預約</p>
      <ValidationObserver ref="orderForm">
        <div class="my-2 flex items-center gap-2 text-left">
            <p class="w-3/12">預約時間</p>
            <div class="flex w-9/12 gap-1">
              <ValidationProvider ref="orderDate" name="日期" rules="required" v-slot="{ errors }" class="w-3/5">
                <VCCalendarSelect :min-date="new Date()" :placeholder="'選擇日期'" v-model="orderDate" :errors="errors"></VCCalendarSelect>
              </ValidationProvider>
              <ValidationProvider ref="timeList" name="時間" rules="required" v-slot="{ errors }" class="w-2/5">
                <C-Select class="w-full" :list="timeList" v-model="orderTime" :placeholder="'選擇時間'" item-text="name" item-value="id" object-return :errors="errors"></C-Select>
              </ValidationProvider>
            </div>
        </div>

        <div class="my-2 flex items-center gap-2 text-left">
            <p class="w-3/12">姓名</p>
            <div class="w-9/12">
              <ValidationProvider ref="name" name="姓名" rules="required" v-slot="{ errors }">
                <C-Input v-model="name" class="mb-0" :errors="errors">
                  <template v-slot:endIcon>
                    <font-awesome-icon v-if="name !== ''" @click="name = ''" icon="fa-regular fa-xmark" class=" text-rose-600"/>
                  </template>
                </C-Input>
              </ValidationProvider>
            </div>
        </div>

        <div class="my-2 flex items-center gap-2 text-left">
            <p class="w-3/12">電話</p>
            <div class="w-9/12">
              <ValidationProvider ref="cellphone" name="電話" rules="required" v-slot="{ errors }">
                <C-Input v-model="cellphone" class="mb-0" :errors="errors">
                  <template v-slot:endIcon>
                    <font-awesome-icon v-if="cellphone !== ''" @click="cellphone = ''" icon="fa-regular fa-xmark" class=" text-rose-600"/>
                  </template>
                </C-Input>
              </ValidationProvider>
            </div>
        </div>

        <div class="my-2 flex items-center gap-2 text-left">
          <p class="w-3/12">指定預約</p>
          <div class="flex w-9/12 flex-nowrap gap-1 text-left">
            <C-Checkbox-Full v-model="isOrderDesigner" class="!ml-0 !w-fit"></C-Checkbox-Full>
          </div>
        </div>

        <div class="my-2 flex gap-2 text-left">
            <label class="w-3/12">備註</label>
            <div class="relative flex w-9/12 flex-nowrap gap-1">
              <textarea name="remark" id="" v-model="remark" rows="5" class="w-full rounded-md border border-primary/50"></textarea>
              <font-awesome-icon v-if="remark !== ''" @click="remark = ''" icon="fa-regular fa-xmark" class=" absolute right-2 bottom-1 text-rose-600"/>
            </div>
        </div>

        <div class="fixed bottom-[100px] w-[calc(100%-24px)]">
          <div class="w-full cursor-pointer bg-second py-2 px-4 text-white" @click="doFormValidate" @keypress="doFormValidate">新增預約</div>
        </div>
      </ValidationObserver>
    </section>
  </main>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import VCCalendarSelect from '../../../components/general/VC-CalendarSelect.vue';

export default {
  components: { VCCalendarSelect },
  name: 'order-add',
  description: '預約的首頁',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 預約',
  },
  data() {
    return {
      orderDate: new Date(),
      orderTime: '',
      name: '',
      cellphone: '',
      remark: '',
      isOrderDesigner: true,
      timeList: [],
    };
  },
  methods: {
    doRoute(item) {
      this.$router.push({ name: item.name });
    },
    async doFormValidate() {
      const isValidForm = await this.$refs.orderForm.validate();
      if (!isValidForm) return;
      this.doDelivery();
    },
    async doDelivery() {
      const submitForm = {
        designerId: this.userInfo.user.id,
        dateTime: this.$dayjs(`${this.$dayjs(this.orderDate).format('YYYY-MM-DD')} ${this.orderTime.name}`).valueOf(),
        cellphone: this.cellphone,
        merchantId: this.currentMechantId,
        name: this.name,
        remark: this.remark,
        isDesignate: this.isOrderDesigner,
      };
      const resp = await this.$api.bookingV2(submitForm);
      const { errors } = resp.data;
      if (errors) return;
      this.fireSwalToOrder();
      // await this.getTimeList();
    },
    fireSwalToOrder() {
      this.$swal.fire({
        title: ' ',
        html:
        `<div class="flex items-center justify-center my-10">
          <img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>新增成功</span>
        </div>`,
        confirmButtonText: '回列表',
        showConfirmButton: true,
        showCloseButton: true,
        customClass: {
          confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
        },
      }).then((res) => {
        if (res.isConfirmed) {
          this.doRoute({ name: 'ScheduleBreak' });
        }
      });
    },
    async getTimeList() {
      this.orderTime = ''; // 原本設計是只要重打就把時間清除為了把送出成功之後把資料清空
      // adjust sbbmitForm for different api by isOrderDesigner
      const submitForm = () => (this.isOrderDesigner ? {
        date: this.$dayjs(this.orderDate).valueOf(),
        storeId: this.currentMechantId,
        designerId: this.userInfo.user.id,
      } : {
        storeId: this.currentMechantId,
        date: this.$dayjs(this.orderDate).valueOf(),
      });
      // adjust response schema by isOrderDesigner
      const respDataSchema = 'getBookingTimes';
      const resp = await this.$api[respDataSchema](submitForm());
      const { data, errors } = resp.data;
      if (errors) return;
      this.timeList = Array.isArray(data[respDataSchema])
        ? data[respDataSchema].map((item, index) => ({
          name: this.$dayjs(item.time).isValid() ? this.$dayjs(item.time).format('HH:mm') : item.time,
          id: `bookingTime${index}`,
          disabled: false,
          // disabled: item.status !== '可預約',
        }))
        : [];
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
  },
  watch: {
    orderDate: {
      immediate: true,
      handler() {
        this.getTimeList();
      },
    },
    // isOrderDesigner() {
    //   this.getTimeList();
    // },
  },
};
</script>

